import React from 'react'
import Nav from '../components/common/nav'
import Footer from '../components/common/footer'
import Head from 'next/head'
import HowiItWorks from '../components/common/howitworks'
import {Collapse} from 'antd'
import config from '../config'
const {Panel} = Collapse;
export default () => {
    return (
        <div className="bg-gray-100" style={{backgroundImage: "url('/static/img/bg.svg')"}}>
            <Head>
                <title>CryptoFair - The easiest and fastest way to make money in crypto industry</title>
            </Head>
            <div className="bg-white">
                <div className="container mx-auto px-4">
                    <Nav />
                </div>
            </div>
            <div className="container mx-auto px-4 py-32">
                <div className="mb-8">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">How It Works</p>
                    <div className="text-lg font-bold text-center mb-16">Four simple steps</div>
                    <HowiItWorks />
                </div>
                <div className="">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">Detailed instructions</p>
                    <div className="">
                        <div className="flex flex-wrap mb-8">
                            <div className="w-full md:w-32">
                                <img src="/static/0.svg" alt="" className="w-full"/>
                            </div>
                            <div className="py-4">
                                <div className="font-bold text-xl">0. Ethereum</div>
                                <div className="font-bold text-lg">0.0.</div>
                                <div className="text-lg">If you haven't an Ethereum wallet, read more about it in <a href="/faqeth" className="">Ethereum FAQ</a></div>
                            </div>
                        </div>
                        <div className="flex flex-wrap">
                            <div className="w-full md:w-32">
                                <img src="/static/signin.png" alt="" className="w-full"/>
                            </div>
                            <div className="w-full md:flex-1 md:w-auto py-4">
                                <div className="font-bold text-xl">1. Sign up</div>
                                <div className="font-bold text-lg">1.1.</div>
                                <div className="">
                                    <div className="text-center"><img src="/static/Signup1.png" alt="" className="mx-auto"/></div>
                                </div>
                                <div className="text-lg">
                                    Send 0.08 ETH from your Ethereum wallet to your upline. And the first user came after you will return this amount back to you.
                                </div>
                                <div className="text-lg">For that send 0.08 ETH to the <a href="" className="">smart contract address</a> and enter a number of your upline Ethereum-wallet to the "Input DATA" MANDATORY field (otherwise, the smart contract will not accept the transfer and the funds will remain with you). If you use MetaMask, the transaction is done in 1 click, and the address of your upline will be inserted into the "Input DATA " field AUTOMATICALLY.</div>
                                <div className="text-lg">The smart contract instantly redirects the funds to your first-line upline (your direct inviter), and assigns you the first level in the system. A successful transaction will register you in the System and assign the 1st level to Your account (more details about the levels below).</div>
                                <div className="text-lg">Simple and clear guide on the page <a href={config.signup} className="">Sign up</a></div>
                                <div className="font-bold text-lg">1.2.</div>
                                <div className="">
                                    <div className="text-center">
                                        <img src="/static/Signup2.png" alt="" className=""/>
                                    </div>
                                </div>
                                <div className="text-lg">These 0.05 ETH will return to you fully after the first referral.</div>
                            </div>
                        </div>
                        <div className="flex flex-wrap">
                            <div className="w-full md:w-32">
                                <img src="/static/Login.png" alt="" className=""/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    )
}